  .sub-menu {
    // width: $sub-menu-width;
    width: 100% !important;
    // background: $foreground-color;
    // position: fixed;
    // left: $main-menu-width;
    // border-left: 1px solid $separator-color-light;
    @include depth(2);
    box-shadow: none !important;
    transition: transform $menu-collapse-time;
    // height: calc(100% - #{$navbar-height});
    height: 100%;

    padding-top: 10px;
    padding-bottom: 10px;
    padding-right: 0;

    .scroll {
      margin-right: unset;
      padding-right: unset;
      height: 100%;

      .ps__thumb-y {
        right: 0;
      }

      .scrollbar-container {
        padding-top: 15px;
        padding-bottom: 15px;
      }
    }

    ul {
      display: none;

      li {
        margin-bottom: 0;
        margin-left: 30px;
        position: relative;

        a {
          font-size: 13px;
          display: block;
          padding: 8px 0;
          margin-bottom: 10px;
        }

        i {
          font-size: 1.3em;
          margin-right: 10px;
          color: $secondary-color;
          vertical-align: middle;
          display: inline-block;
        }

        &.active i,
        &.active a {
          color: $theme-color-1;
        }

        i::before{
          margin:0;
        }

        &.active:after {
          content: ' ';
          background: $theme-color-1;
          border-radius: 10px;
          position: absolute;
          width: 4px;
          height: 4px;
          top: 50%;
          transform: translateY(-50%);
          left: -16px;
        }

        span {
          vertical-align: middle;
          padding-top: 3px;
          display: inline-block;
        }
      }

      li.has-sub-item {
        &.active:after {
          content: ' ';
          background: initial;
        }
      }

      ul.third-level-menu {
        display: block !important;
        li {
          a,
          i {
            color: $primary-color;
          }
          a:hover,
          &.active i,
          &.active a {
            color: $theme-color-1;
          }

          &.active:after {
            left: -22px;
          }
        }
      }

      .rotate-arrow-icon {
        i {
          transition: transform $animation-time-short;
          transform: rotate(0);
        }

        &.collapsed {
          i {
            transform: rotate(-90deg);
          }
        }
      }
    }

    @include respond-below(xl) {
      // left: $main-menu-width-lg;
      width: $sub-menu-width-lg;
      // height: calc(100% - #{$navbar-height-lg});
      height: 100%;
    }

    @include respond-below(lg) {
      // left: $main-menu-width-md;
      width: $sub-menu-width-md;
      // height: calc(100% - #{$navbar-height-md});
      height: 100%;

    }

    @include respond-below(sm) {
      // left: $main-menu-width-xs;
      width: $sub-menu-width-xs;
      // height: calc(100% - #{$navbar-height-xs});
      height: 100%;
      box-shadow: none !important;
    }
  }

  @include respond-below(xl) {
    // padding-top: $navbar-height-lg;
    // height: calc(100% - #{$navbar-height-lg});
    height: 100%;
